.premium-page{
  background: #fff;
  line-height: 1.6;
  font-family: 'Noto Sans JP', "ヒラギノ角ゴPro", "Hiragino Kaku Gothic Pro", "メイリオ", "Meiryo", sans-serif;
  color: #292929;
  font-size: 16px;
  font-weight: 400;
  -ms-text-size-adjust: 100%; /* 2 */
  -webkit-text-size-adjust: 100%; /* 2 */
  @media screen and (max-width:768px){
    font-size: 14px;
  }
}
// header
#header {
	box-shadow: 0px 3px 6px rgb(0 0 0 / 10%);
	padding: 8px 90px 9px;
	position: fixed;
	width: 100%;
	min-width: 1080px;
	z-index: 9999;
	background: #fff;
	top: 0;
  @media screen and (max-width:768px){
    min-width: 0;
		padding: 7px 10px;
  }
  @media screen and (max-width:640px){
    padding: 7px 10px;
  }
}
#header .container-fluid{
	padding: 0;
}
#header .h-wrap{
  @media screen and (max-width:992px){
    display: block!important;
		overflow: hidden;
  }
}
#header #h_logo{
	width: 144px;
	padding: 12px 0;
  @media screen and (max-width:992px){
    float: left;
		margin: 0!important;
  }
  @media screen and (max-width:768px){
    width: 100px;
		padding: 10px 0;
  }
  @media screen and (max-width:640px){
    width: 80px;
  }
}

#header .menu_trigger{
  @media screen and (max-width:768px){
    float: right;
    width: 40px;
  }
}

#header .sp{
  display: none;
  @media screen and (max-width:768px){
    display: block;
  }
}

#header .nav-wrap{
  @media screen and (max-width:992px){
    float: right;
  }
  @media screen and (max-width:768px){
    display: none;
		background: #fff;
		float: none;
		width: 100%;
		max-width: none;
		overflow: hidden;
		padding: 20px 0;
  }
}
#header .nav {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	margin:0!important;
  @media screen and (max-width:992px){
    margin: 0!important;
  }
  @media screen and (max-width:768px){
    display: block;
  }
}
#header .nav li{
	margin-right: 8px;
  @media screen and (max-width:768px){
    margin-right: 0;
  }
  @media screen and (max-width:640px){
    margin-right: 3px;
  }
}
#header .nav li.user-nav{
	margin-right: 0;
	margin-left: 14px;
}
#header .nav li a{
	text-indent: -9999px;
}
#header .nav li a{
	display: block;
	width: 51px;
	box-sizing: border-box;
	border: 2px solid #474747;
	background-image: url(../img/Icon-search.png);
	background-repeat: no-repeat;
	background-position: center;
	background-size: 17.35px;
	border-radius: 22px;
	padding: 0 2px;
	background-color: #fff;
	transition: all ease .3s;
  @media screen and (max-width:768px){
    width: 200px;
		background-size: 12px;
		padding: 7px 10px;
		margin: 0 auto 10px;
  }
}
#header .nav li a:hover{
	background-color: #FF6536;
	border-color: #FF6536;
	background-image: url(../img/Icon-search-hover.png);
}
#header .nav li:nth-child(2) a{
	background-image: url(../img/Icon-tin.png);
}
#header .nav li:nth-child(3) a{
	background-image: url(../img/Icon-message.png);
}
#header .nav li:nth-child(4) a{
	background-image: url(../img/Icon-metro.png);
}
#header .nav li:nth-child(2) a:hover{
	background-image: url(../img/Icon-tin-hover.png);
}
#header .nav li:nth-child(3) a:hover{
	background-image: url(../img/Icon-message-hover.png);
}
#header .nav li:nth-child(4) a:hover{
	background-image: url(../img/Icon-metro-hover.png);
}
#header .nav li.user-nav a{
	text-indent: 0;
	width: auto;
	border: 0;
	background-image: none;
	font-size: 16px;
	color: #474747;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
	text-transform: capitalize;
}
#header .nav li.user-nav a:hover{
	background-color:#fff;
}
#header .nav li.user-nav img{
  display: block;
	width: 53px;
	float: left;
	margin-right: 10px;
  @media screen and (max-width:768px){
    width: 40px;
    margin-right: 4px;
  }
  @media screen and (max-width:640px){
    width: 35px;
		margin-right: 4px;
  }
}
#header .nav li.user-nav{
  position: relative;
  @media screen and (max-width:768px){
    margin-left: 0;
  }
  @media screen and (max-width:768px){
    margin-left: 4px;
  }
}
#header .nav li.user-nav a{
  @media screen and (max-width:768px){
    font-size: 110%;
    margin-bottom: 0;
  }
  @media screen and (max-width:640px){
    font-size: 100%;
  }
}
#header .user-area{
  display: inline-flex;
  flex-direction: column;
}
#header .premium-badge{
  font-size: 15px;
  font-weight: bold;
  color: #FF6536;
  line-height: 1.4;
  padding-left: 17px;
  background-image: url(../img/Icon-star.png);
	background-repeat: no-repeat;
	background-position: left;
	background-size: 14.36px;
}

// contents
#contents{
  position: relative;
  z-index: 2;
}
#contents .main{
  min-width: 1080px;
  @media screen and (max-width:768px){
    min-width: 0;
  }
}
// page
#page{
  margin: 70px auto 0;
  padding-bottom: 216px;
  @media screen and (max-width:768px){
    margin: 60px auto 0;
    padding-bottom: 108px;
  }
  @media screen and (max-width:640px){
    width: 95%;
  }
}

// inner
.inner{
  max-width: 1140px;
  margin-right: auto;
  margin-left: auto;
}

.mv-what{
  padding-top: 91px;
  @media screen and (max-width:768px){
    padding-top: 60px;
  }
}
.mv-crew{
  padding-top: 60px;
  padding-bottom: 60px;
  background-color: #F5F4ED;
  margin-right: calc(50% - 50vw);
  margin-left: calc(50% - 50vw);
  padding-right: calc(50vw - 50%);
  padding-left: calc(50vw - 50%);
}
.mv-help{
  padding-top: 101px;
  padding-bottom: 114px;
  background-color: #F5F4ED;
  margin-right: calc(50% - 50vw);
  margin-left: calc(50% - 50vw);
  padding-right: calc(50vw - 50%);
  padding-left: calc(50vw - 50%);
}

.mv-btn-wrapper{
  text-align: center;
  margin-top: 40px;
}

// level2-heading
.level2-heading{
  text-align: center;
  &__title{
    font-size: 40px;
    font-weight: 500;
    margin-bottom: 0;
    @media screen and (max-width:768px){
      font-size: 30px;
    }
  }
  &::after{
    content: '';
    display: block;
    width: 249px;
    height: 106px;
    background-image: url(../img/flag-img.png);
    background-size: cover;
    background-position: center;
    margin-top: 10px;
    margin-right: auto;
    margin-left: auto;
  }
  @media screen and (max-width:768px){
    &::after{
      width: 200px;
      height: 85px;
    }
  }
}
.level2-heading--crew{
  &::before{
    content: '';
    display: block;
    width: 95px;
    height: 100px;
    background-image: url(../img/Icon-mv-crew.png);
    background-size: cover;
    background-position: center;
    margin-right: auto;
    margin-left: auto;
  }
  @media screen and (max-width:768px){
    &::before{
      width: 80px;
      height: 84px;
    }
  }
  .level2-heading__title{
    margin-top: 11px;
  }
}
.level2-heading--help{
  &::before{
    content: '';
    display: block;
    width: 93px;
    height: 86px;
    background-image: url(../img/Icon-mv-help.png);
    background-size: cover;
    background-position: center;
    margin-right: auto;
    margin-left: auto;
  }
  @media screen and (max-width:768px){
    &::before{
      width: 80px;
      height: 74px;
    }
  }
  .level2-heading__title{
    margin-top: 24px;
  }
}
// level3-heading
.level3-heading{
  padding-left: 31px;
  position: relative;
  font-size: 32px;
  font-weight: 400;
  margin-bottom: 0;
  @media screen and (max-width:768px){
    font-size: 24px;
  }
  &::before{
    content: '';
    display: inline-block;
    position: absolute;
    top: .3em;
    left: 0;
    background-size: cover;
    background-position: center;
  }
  @media screen and (max-width:768px){
    &::before{
      top: .15em;
    }
  }
}
.level3-heading--blue{
  &::before{
    width: 20px;
    height: 20px;
    background-image: url(../img/Icon-arrow-blue.png);
  }
}
.level3-heading--orange{
  &::before{
    width: 24px;
    height: 29px;
    background-image: url(../img/Icon-arrow-orange.png);
  }
}
.help-border{
  padding-bottom: 10px;
  background-image: linear-gradient(to right,#FF9149,#FF9149 17.5px,transparent 10.5px,transparent 10.5px);
  background-size: 28px 1px;
  background-position: left bottom;
  background-repeat: repeat-x;
}
.crew-icon-heading{
  padding-left: 46px;
  &::before{
    top: .15em;
    width: 36px;
    height: 38px;
    background-image: url(../img/Icon-crew-sm.png);
  }
  @media screen and (max-width:768px){
    &::before{
      top: 0;
    }
  }
}
.level3-heading-new{
  padding-left: 55px;
  position: relative;
  font-size: 32px;
  font-weight: 400;
  margin-bottom: 0;
  @media screen and (max-width:768px){
    font-size: 24px;
  }
  &::before{
    content: '';
    display: inline-block;
    position: absolute;
    left: 0;
    background-size: cover;
    background-position: center;
  }
}
.level3-heading-new--home{
  &::before{
    top: -.32em;
    width: 35px;
    height: 44px;
    background-image: url(../img/Icon-home.png);
  }
  @media screen and (max-width:768px){
    &::before{
      top: -.8em;
    }
  }
}
.level3-heading-new--credit{
  &::before{
    top: .25em;
    width: 35px;
    height: 27px;
    background-image: url(../img/Icon-cregit.png);
  }
  @media screen and (max-width:768px){
    &::before{
      top: 0;
    }
  }
}
.level3-heading-new--premium{
  &::before{
    top: .33em;
    width: 49px;
    height: 23px;
    background-image: url(../img/icon-premium.png);
  }
  @media screen and (max-width:768px){
    &::before{
      top: .15em;
    }
  }
}
.level3-heading-new--return{
  &::before{
    top: 0;
    width: 37px;
    height: 37px;
    background-image: url(../img/icon-return.png);
  }
  @media screen and (max-width:768px){
    &::before{
      top: -.4em;
    }
  }
}
.famiful-img{
  position: relative;
  margin-top: 20px;
  margin-right: 0;
  margin-bottom: 0;
  margin-left: 0;
  &::before{
    content: '';
    display: block;
    padding-top: 40.54035%;
  }
  img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.mv-body{
  margin-top: 22px;
}
.mv-text{
  margin-bottom: 0;
  line-height: 2;
  &:nth-of-type(n + 2){
    margin-top: 22px;
  }
}

#page .page-list{
  margin-bottom: 0;
  padding-left: 0;
}
#page .page-item{
  margin-top: 60px;
}

.page-body{
  margin-top: 21px;
  padding-left: 30px;
  line-height: 2;
}

.crew-body{
  padding-left: 46px;
}

.body-img{
  position: relative;
  &::after{
    content: '';
    display: inline-block;
    position: absolute;
    bottom: 0;
    right: 251px;
    height: 104px;
    width: 104px;
    background-image: url(../img/pirate-img.png);
    background-size: cover;
    background-position: center;
  }
  @media screen and (max-width:768px){
    &::after{
      display: block;
      position: relative;
      margin-top: 20px;
      bottom: initial;
      right: initial;
      margin-right: 0;
      margin-left: auto;
    }
  }
}
.page-title{
  text-align: center;
  font-size: 30px;
  margin-top: 30px;
  margin-bottom: 0;
  @media screen and (max-width:768px){
    font-size: 20px;
  }
}
.page-text{
  margin-bottom: 0;
}
.border-text{
  margin-top: 30px;
}
.note-text{
  margin-top: 40px;
}
.last-text{
  margin-top: 15px;
}

#page .child-page-list{
  margin-bottom: 0;
  padding-left: 0;
}

.border-list{
  margin-top: 30px;
}

#page .child-border-item{
  padding-top: 5px;
  padding-bottom: 5px;
  background-image: linear-gradient(to right,#FF9149,#FF9149 17.5px,transparent 10.5px,transparent 10.5px);
  background-size: 28px 1px;
  background-position: left bottom;
  background-repeat: repeat-x;
}

#page .child-crew-item{
  &:nth-of-type(n + 2){
    margin-top: 30px;
  }
}

.feature-list{
  margin-top: 30px;
  padding-left: 0;
  margin-bottom: -60px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.feature-item{
  margin-bottom: 60px;
  width: calc(100% / 4);
  text-align: center;
  &:nth-of-type(4n + 1){
    color: #C2C923;
  }
  &:nth-of-type(4n + 2){
    color: #CB1B4A;
  }
  &:nth-of-type(4n + 3){
    color: #42AFB6;
  }
  &:nth-of-type(4n + 4){
    color: #FCB414;
  }
  @media screen and (max-width:768px){
    width: calc(100% / 2);
  }
}

.feature-img-wrapper{
  margin-right: auto;
  margin-left: auto;
  margin-bottom: 0;
  width: 100px;
  height: 100px;
  img{
    width: 100%;
  }
}

.feature-text{
  margin-top: 30px;
  line-height: 1.5;
  margin-bottom: 0;
}

.feature-note{
  font-size: 14px;
  line-height: 1.5;
  margin-top: 20px;
  margin-bottom: 0;
}

.step-list{
  margin-top: 30px;
  display: flex;
  justify-content: space-between;
  @media screen and (max-width:768px){
    flex-direction: column;
  }
}

.step-list + .page-title{
  margin-top: 60px;
}

.step-item{
  width: calc((100% - 45px)/3);
  margin-right: 35px;
  &:nth-of-type(3n){
    margin-right: 0;
  }
  @media screen and (max-width:768px){
    width: 80%;
    max-width: 100%;
    margin-right: auto;
    margin-left: auto;
    &:nth-of-type(n + 2){
      margin-top: 30px;
    }
    &:nth-of-type(3n){
      margin-right: auto;
    }
  }
}

.step-img-wrapper{
  width: 180px;
  height: 117px;
  margin-right: auto;
  margin-left: auto;
  img{
    width: 100%;
  }
}

.step-title{
  margin-bottom: 0;
  margin-top: 20px;
  font-size: 20px;
}

.step-title + p{
  margin-top: 15px;
}

.step-text{
  line-height: 2;
  margin-bottom: 0;
}

.step-btn-wrapper{
  text-align: center;
  margin-top: 60px;
}

.faq-text + .faq-title{
  margin-top: 30px;
}

.faq-title{
  font-weight: 400;
}

.faq-text{
  margin-top: 10px;
  padding-left: 32px;
}

.faq-btn-wrapper{
  margin-top: 60px;
  text-align: center;
}

.sdgs-img-wrapper{
  position: relative;
  margin-top: -46px;
  @media screen and (max-width:768px){
    margin-top: -0.46vw;
  }
  &::before{
    content: '';
    display: block;
    padding-top: 70.70701%;
  }
  img{
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.sdgs-body{
  line-height: 2;
  margin-top: -30px;
  @media screen and (max-width:640px){
    margin-top: -10px;
  }
}

.sdgs-img-list{
  margin-top: 22px;
  display: flex;
  flex-wrap: wrap;
  padding-left: 0;
  margin-left: -20px;
  margin-bottom: -20px;
}

.sdgs-img-item{
  width: 157px;
  height: 157px;
  margin-left: 20px;
  margin-bottom: 20px;
  @media screen and (max-width:768px){
    width: 120px;
    height: 120px;
  }
  @media screen and (max-width:640px){
    width: 100px;
    height: 100px;
  }
  img{
    width: 100%;
  }
}

.sdgs-text-body{
  margin-top: 22px;
  line-height: 2;
}

.icon-crew{
  margin-right: 15px;
  margin-left: 15px;
  &::before{
    content: '';
    display: inline-block;
    width: 30px;
    height: 42px;
    background-image: url(../img/Icon-crew-badge.png);
    background-size: cover;
    background-position: center;
    vertical-align: middle;
  }
}

.horizontal-btn-list{
  margin-top: 60px;
  padding-left: 0;
  display: flex;
  justify-content: center;
  @media screen and (max-width:768px){
    flex-direction: column;
    align-items: center;
  }
}

.horizontal-btn-item{
  flex: 1 1 0;
  max-width: 264px;
  &:nth-of-type(n + 2){
    margin-left: 30px;
  }
  @media screen and (max-width:768px){
