}

.domain-wrapper{
  margin-top: 60px;
  text-align: center;
}

.domain-block{
  display: inline-block;
  width: 75.08771%;
  text-align: center;
  padding: 13px 10px;
  background-color: rgba(197,197,197,.42);
  border: 5px solid rgba(115,114,252,.42);
  border-radius: 10px;
  font-size: 20px;
  line-height: 1.5;
  @media screen and (max-width:768px){
