.domain-text{
  color: #FF6536;
}

.help-link{
  color: #7372FC;
  font-size: 16px;
  line-height: 2;
  text-decoration: none;
  @media screen and (max-width:768px){
    font-size: 14px;
  }
}

.help-img-wrapper{
  margin-top: 30px;
  width: 650px;
  max-width: 100%;
  margin-right: auto;
  margin-left: auto;
  position: relative;
  &::before{
    content: '';
    display: block;
    padding-top: 42.85692%;
  }
  img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

// btn
#page .btn{
  font-size: 16px;
  display: block;
  border-radius: 28px;
  width: 264px;
  padding: 16px 10px;
  box-sizing: border-box;
  margin: 0 auto;
  transition: all ease .3s;
  text-align: center;
  border: 1px solid;
  @media screen and (max-width:768px){
    font-size: 100%;
    width: 180px;
    padding: 14px 10px;
  }
}

#page .btn_submit{
  color: #fff;
  background: #FF6536;
  border-color: #FF6536;
  &:focus,&:hover{
    color: #FF6536;
    background: #fff;
  }
}

// footer
#footer{
	background: #F5F4ED;
	padding: 40px 0 25px;
	overflow: visible;
	display: table;
	width: 100%;
	box-sizing: border-box;
	position: relative;
	min-width: 1080px;
  @media screen and (max-width:768px){
    min-width: 0;
  }
}
.no-sidebar #footer{
	padding-left: 0;
}
/************* ページトップ *******************************/
#pagetop {
	position: absolute;
	right: 2%;
	top: -170px;
	z-index: 9999;
  @media screen and (max-width:992px){
		right: 60px;
		top: -100px;
  }
  @media screen and (max-width:768px){
    top: -40px;
		right: 30px;
  }
}
